/**
 * Input Panel Component
 * Text input and microphone button at bottom center
 */

import React, { useState, useRef, useEffect } from 'react';
import { useAppStore } from '../store/useAppStore';
import { speechController } from '../services/SpeechController';
import { aiService } from '../services/AIService';
import { motionManager } from '../services/MotionManager';
import { synchronizationCoordinator } from '../services/SynchronizationCoordinator';
import './InputPanel.css';

export const InputPanel: React.FC = () => {
  const [inputText, setInputText] = useState('');
  const [isProcessing, setIsProcessing] = useState(false);
  const inputRef = useRef<HTMLInputElement>(null);

  const {
    isRecording,
    isSpeaking,
    setRecording,
    setLeftPanelContent,
    setTeachingSegments,
    setIsTeaching,
  } = useAppStore();

  useEffect(() => {
    // Update motion manager when recording state changes
    motionManager.setListening(isRecording);
  }, [isRecording]);

  const handleSubmit = async (text: string) => {
    if (!text.trim() || isProcessing) return;

    setIsProcessing(true);
    setInputText('');

    try {
      // Get AI response
      const response = await aiService.query(text);

      // Store segments
      setTeachingSegments(response.segments);
      setIsTeaching(true);

      // Execute synchronized teaching sequence
      await synchronizationCoordinator.executeTeachingSequence(
        response.segments,
        response.text,
        response.images || []
      );

    } catch (error) {
      console.error('Error processing input:', error);
      setLeftPanelContent('Sorry, I encountered an error. Please try again.');
    } finally {
      setIsProcessing(false);
    }
  };

  const handleMicClick = async () => {
    if (isRecording) {
      // Stop recording
      setRecording(false);
      
      try {
        const audioBlob = await speechController.stopRecording();
        const transcribedText = await speechController.transcribeAudio(audioBlob);
        
        if (transcribedText) {
          await handleSubmit(transcribedText);
        }
      } catch (error) {
        console.error('Error processing voice input:', error);
      }
    } else {
      // Start recording - interrupt any active teaching
      synchronizationCoordinator.interrupt();
      
      try {
        await speechController.startRecording();
        setRecording(true);
      } catch (error) {
        console.error('Error starting recording:', error);
        alert('Failed to access microphone. Please check permissions.');
      }
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSubmit(inputText);
    }
  };

  return (
    <div className="input-panel">
      <div className="input-container">
        <input
          ref={inputRef}
          type="text"
          className="text-input"
          placeholder="Ask me anything..."
          value={inputText}
          onChange={(e) => setInputText(e.target.value)}
          onKeyPress={handleKeyPress}
          disabled={isRecording || isProcessing}
        />
        
        <button
          className={`mic-button ${isRecording ? 'recording' : ''}`}
          onClick={handleMicClick}
          disabled={isProcessing}
          title={isRecording ? 'Stop recording' : 'Start recording'}
        >
          {isRecording ? (
            <span className="mic-icon recording-icon"></span>
          ) : (
            <span className="mic-icon"></span>
          )}
        </button>

        <button
          className="send-button"
          onClick={() => handleSubmit(inputText)}
          disabled={!inputText.trim() || isProcessing || isRecording}
        >
          {isProcessing ? '' : ''}
        </button>
      </div>

      {isRecording && (
        <div className="recording-indicator">
          <span className="pulse"></span>
          Recording...
        </div>
      )}

      {isSpeaking && (
        <div className="speaking-indicator">
          <span className="wave"></span>
          Haru is speaking...
        </div>
      )}
    </div>
  );
};
