/**
 * Live2D Canvas Component
 * Renders Haru character using PixiJS and pixi-live2d-display
 */

import React, { useEffect, useRef } from 'react';
import * as PIXI from 'pixi.js';
import { Live2DModel } from 'pixi-live2d-display';
import { motionManager } from '../services/MotionManager';
import { useAppStore } from '../store/useAppStore';

// Register PIXI with Live2D
(window as any).PIXI = PIXI;

interface Live2DCanvasProps {
  modelPath: string;
}

export const Live2DCanvas: React.FC<Live2DCanvasProps> = ({ modelPath }) => {
  const canvasRef = useRef<HTMLDivElement>(null);
  const appRef = useRef<PIXI.Application | null>(null);
  const modelRef = useRef<Live2DModel | null>(null);

  const { setHaruState } = useAppStore();

  useEffect(() => {
    if (!canvasRef.current) return;

    // Initialize PixiJS Application
    const app = new PIXI.Application({
      view: document.createElement('canvas'),
      width: 800,
      height: 800,
      backgroundColor: 0xffffff,
      backgroundAlpha: 0,
      antialias: true,
      resolution: window.devicePixelRatio || 1,
      autoDensity: true,
    });

    canvasRef.current.appendChild(app.view as HTMLCanvasElement);
    appRef.current = app;

    // Load Live2D Model
    loadModel(app, modelPath);

    // Cleanup
    return () => {
      if (modelRef.current) {
        modelRef.current.destroy();
      }
      if (appRef.current) {
        appRef.current.destroy(true, { children: true, texture: true, baseTexture: true });
      }
    };
  }, [modelPath]);

  const loadModel = async (app: PIXI.Application, path: string) => {
    try {
      console.log('Loading Live2D model from:', path);

      // Load model
      const model = await Live2DModel.from(path);
      modelRef.current = model;

      // Scale and position model
      const scale = Math.min(
        app.screen.width / model.width,
        app.screen.height / model.height
      ) * 0.8;

      model.scale.set(scale);
      model.x = app.screen.width / 2;
      model.y = app.screen.height / 2;
      model.anchor.set(0.5, 0.5);

      // Add to stage
      app.stage.addChild(model);

      // Initialize motion manager
      motionManager.setModel(model);

      // Set initial state
      setHaruState('idle');

      console.log('Live2D model loaded successfully');

      // Enable interaction
      model.on('hit', (hitAreas: string[]) => {
        console.log('Hit areas:', hitAreas);
        // Can trigger special animations on click
      });

    } catch (error) {
      console.error('Failed to load Live2D model:', error);
    }
  };

  return (
    <div
      ref={canvasRef}
      style={{
        width: '100%',
        height: '100%',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      }}
    />
  );
};
